<template>
  <div>
    <div id="iavContainer"></div>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'
  import { refreshIav, dwollaIav } from 'utils/aero_functions'
  export default {
    name: 'iav-page',
    mounted () {
      refreshIav(this.idToken)
        .then(iavToken => {
          dwollaIav(iavToken)
        })
        .catch(err => {
          dwollaIav(err)
        })
    },
    methods: {
      ...mapMutations({
        SET_IAV: 'user/SET_IAV'
      })
    },
    computed: {
      ...mapGetters(['iavToken', 'idToken'])
    }
  }
</script>

<style lang="scss" scoped>
  /* Implement Dwolla's styles */
  .dwolla-iav-text-box,
  .dwolla-iav-button,
  .dwolla-iav-header,
  .dwolla-iav-text,
  .dwolla-iav-link,
  .dwolla-iav-error,
  .dwolla-iav-label {
      font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
      font-weight: 300;
  }

  .dwolla-iav-header {
      font-weight: 300;
      margin-bottom: 2.5rem;
  }

  .dwolla-iav-text-box {
      box-shadow: none;
      border: 1px solid #ddd;
      border-left: 0;
      border-right: 0;
      border-top: 0;
      border-radius: 0;
  }

  .dwolla-iav-text-box:focus {
      border-color: #1e88e5;
  }

  .dwolla-iav-link {
      color: #1e88e5;
      cursor: pointer;
  }

  .dwolla-iav-link:hover {
      color: #1565c0;
  }

  .dwolla-iav-button {
      background: #1e88e5;
      color: white;
      border-radius: 3px;
  }

  .dwolla-iav-button:hover {
      background: #4D8CD4;
      cursor: pointer;
  }

  .dwolla-iav-button:active {
      background: #1565c0;
  }

  .dwolla-iav-check-image {
      background: #1e88e5;
  }

  .dwolla-iav-radio-hover {
      background-color: #fff;
  }

  .dwolla-iav-radio-selected {
      background-color: #1e88e5;
  }
</style>



